




















import { Component, Prop, Vue } from 'vue-property-decorator';
import Loading from '@/components/Loading.vue';

@Component({
  components: {
    Loading,
  },
})
export default class InputMessage extends Vue {
  @Prop({
    type: Boolean,
    default: false,
  })
  pending!: boolean;

  value = '';

  handleSend(): void {
    if (this.value && !this.pending) {
      this.$emit('send', this.value);
      this.value = '';
    }
  }
}
